<template>
  <section class="service_section">
    <div class="card">
      <router-link to="/service">
        <div class="card_text_wepapp">
          <v-row>
            <v-col cols="7" style="padding: 0">
              <p class="card_title">Web / APP</p>
              <p class="card_title_2">Development</p>
              <p class="card_desc">
                게임인스는 이러한 서비스를 제공하고 있습니다.
              </p>
            </v-col>
            <v-col cols="5" style="padding: 0">
              <div class="logo_btn">
                <span class="line_1"></span>
                <span class="line_2"></span>
              </div>
            </v-col>
          </v-row>
        </div>
      </router-link>
    </div>
    <div class="card">
      <router-link to="/service">
        <div class="card_text_platform">
          <v-row>
            <v-col cols="6" style="padding: 0">
              <p class="card_title">SERVICE</p>
              <p class="card_title_2">PLATFORM</p>
              <p class="card_desc">플랫폼에 대한 간단한 내용</p>
            </v-col>
            <v-col cols="6" style="padding: 0">
              <div class="logo_btn">
                <span class="line_1"></span>
                <span class="line_2"></span>
              </div>
            </v-col>
          </v-row>
        </div>
      </router-link>
    </div>
    <div class="webapp_img">
      <router-link to="/service">
        <div class="hover_img card">
          <div class="webapp_hover card_text"></div>
        </div>
      </router-link>
    </div>
    <div class="platform_img">
      <router-link to="/service">
        <div class="hover_img card">
          <div class="platform_hover card_text"></div>
        </div>
      </router-link>
    </div>

    <div class="service_bg">
      <v-row>
        <div class="service_text">
          <span class="service_sub_title">SERVICE</span>
          <span class="service_sub_subtitle"
            >게임인스는 서비스를 제공하고 있습니다.</span
          >
          <span class="service_sub_detail">
            <p>
              고객을 위한 웹/앱 맞춤 설계, 유지보수 서비스 제공 다양한플랫폼과
              새로운 기술로
            </p>
            빠르게 변화하는 환경에 능동적으로 대응하여, 커뮤니케이션을 통해
            만족감을 총족시켜드립니다.
          </span>
        </div>
      </v-row>
    </div>
  </section>
  <ScrollNav />
</template>

<script>
import ScrollNav from "../header/HeaderNav.vue";
export default {
  components: {
    ScrollNav,
  },
  mounted() {
    const webapp = document.querySelector(".card_text_wepapp");
    const platform = document.querySelector(".card_text_platform");
    const webapp_img = document.querySelector(".webapp_img");
    const platform_img = document.querySelector(".platform_img");
    const direct_logos = document.querySelectorAll(".logo_btn");
    const line1s = document.querySelectorAll(".line_1");
    const line2s = document.querySelectorAll(".line_2");

    webapp_img.addEventListener("mouseover", () => {
      platform.style.opacity = 0;
      direct_logos.forEach((direct_logo) => {
        direct_logo.style.background = "white";
      });
      line1s.forEach((line1) => {
        line1.style.backgroundColor = "#2085F6";
      });
      line2s.forEach((line2) => {
        line2.style.backgroundColor = "#2085F6";
      });
    });

    webapp_img.addEventListener("mouseout", () => {
      platform.style.opacity = 1;
      direct_logos.forEach((direct_logo) => {
        direct_logo.style.background = "none";
      });
      line1s.forEach((line1) => {
        line1.style.backgroundColor = "white";
      });
      line2s.forEach((line2) => {
        line2.style.backgroundColor = "white";
      });
    });

    platform_img.addEventListener("mouseover", () => {
      webapp.style.opacity = 0;
      direct_logos.forEach((direct_logo) => {
        direct_logo.style.background = "white";
      });
      line1s.forEach((line1) => {
        line1.style.backgroundColor = "#2085F6";
      });
      line2s.forEach((line2) => {
        line2.style.backgroundColor = "#2085F6";
      });
    });

    platform_img.addEventListener("mouseout", () => {
      webapp.style.opacity = 1;
      direct_logos.forEach((direct_logo) => {
        direct_logo.style.background = "none";
      });
      line1s.forEach((line1) => {
        line1.style.backgroundColor = "white";
      });
      line2s.forEach((line2) => {
        line2.style.backgroundColor = "white";
      });
    });
    const section = document.querySelector(".service_section");
    let timeoutId;

    section.addEventListener("wheel", function (e) {
      if (e.deltaY < 0) {
        e.preventDefault();

        clearTimeout(timeoutId); // 이전 timeout 취소
        timeoutId = setTimeout(() => {
          window.scrollTo({
            top: window.scrollY - window.innerHeight,
            behavior: "smooth",
          });
        }, 500);
      } else if (e.deltaY > 0) {
        e.preventDefault();
        clearTimeout(timeoutId); // 이전 timeout 취소
        timeoutId = setTimeout(() => {
          window.scrollTo({
            top: window.scrollY + window.innerHeight,
            behavior: "smooth",
          });
        }, 500);
      }
    });
  },
};
</script>

<style lang="scss" scoped>
