    width: 33.8%;
    height: 50.3vh;
    scale: 1;
    z-index: 10;
    overflow: hidden;
    transition: all 1s ease-in-out;
    // background-attachment: fixed;
    background-image: url(./../../assets/home/service_big_1.svg);

    .card_text {
      position: absolute;
      color: white;
      width: 100%;
    }

    .logo_btn {
      cursor: pointer;
    }
  }
}

.hover_img:hover {
  opacity: 1;
  width: 100%;
  z-index: 14;
  height: 50.3vh;
  left: 0;
  background-size: cover;

  .card_text {
    opacity: 1;
    padding: 18% 0px 0px 9%;
  }

  .logo_btn {
    cursor: pointer;

    .hover_btn {
      width: 70px;
    }
  }
}

.platform_img {
  width: 100vw;
  height: 59vh;
  position: absolute;
  margin-top: 28%;

  .hover_img {
    position: absolute;
    top: -9.1%;
    left: 50%;
    width: 33.8%;
    z-index: 10;
    height: 50.3vh;
    scale: 1;
    overflow: hidden;
    transition: all 1s ease-in-out;
    background-size: cover;
    // background-attachment: fixed;
    background-image: url("./../../assets/home/service_big_2.svg");

    .card_text {
      position: absolute;
      color: white;

      padding: 54% 0px 0px 7%;

      width: 100%;
    }

    .logo_btn {
      cursor: pointer;
    }
  }
}

.platform_img {
  .hover_img:hover {
    opacity: 1;
    width: 100%;
    height: 50.3vh;
    left: 0;
    background-size: cover;

    .card_text {
    }

    .logo_btn {
      cursor: pointer;

      .hover_btn {
        width: 70px;
      }
    }
  }
}

.service_bg {
  position: relative;
  width: 100vw;
  height: 100vh;
  padding: 0% 17%;
  background: rgb(43, 30, 21);
  color: white;
}

.service_text {
  display: grid;
  position: relative;
  margin-top: 18vh;

  .service_sub_title {
    font-family: sans-serif;
    color: white;
    font-size: 1.25rem;
  }

  .service_sub_subtitle {
    font-family: "Pretendard-Regular";
    font-size: 1.825rem;
    padding: 16px;
  }

  .service_sub_detail {
